import React from "react";
import { Link } from "gatsby";

export default ({ render }) => (
  <div className="work__nav">
    <div className="work__nav-disciplines">
      <Link className="work__nav-disciplines-link" to={`/work`} activeClassName="-active"> 
        <h3 className="work__nav-disciplines-text fontSize-p2">All</h3>
      </Link> 

      {render.disciplines.map((discipline, i) => {
        let formatedLink = discipline.split(' ').join('%20'); // for activeClassName
        formatedLink.toLowerCase();

        return (
          <Link className="work__nav-disciplines-link" key={ discipline } to={`/work/${formatedLink}`} activeClassName="-active"> 
            <h3 className="work__nav-disciplines-text fontSize-p2">{ discipline }</h3> 
          </Link>
        )
      })} 
    </div>

    {/* <div className="work__nav-tags">
      {props.render.categories.map((tag, i) => {
        return (
          <Link className="work__nav-tags-link" key={tag} to={ `/work/${tag.toLowerCase()}` }> 
            <h3 className="work__nav-tags-text fontSize-p2">{ tag }</h3> 
          </Link>
        )
      })} 
    </div> */}
  </div>
)