import React, { Component } from "react";

import Layout from "../components/layout";
import WorkNav from "../components/workNav";
import CreateTiles from "../components/createTiles";
import Cookies from "universal-cookie";

import "../styles/work.scss";

export default class Work extends Component {
  filteredTileData;
  allEntries;
  filteredArticles = [];
  categoryIndex;
  lastPath;
  disciplines = [];
  categories = [];
  cookies = new Cookies();

  state = {
    hasData : false,
    hasFilters: false
  }

  componentDidMount(){
    this.allEntries  = this.props.data.craft.entries;

    this.findFilters();
    this.filterArticles();
  }

  findFilters(){
    // let entries = this.allEntries;

    // entries.forEach(({ disciplines, categories }) => {
    //   disciplines.forEach(({ title }) => {
    //     let discipline = title.toLowerCase();

    //     if(this.disciplines.indexOf(discipline) === -1){
    //       this.disciplines.push(discipline);
    //     }
    //   })

    //   categories.forEach(({ title }) => {
    //     let category = title.toLowerCase();

    //     if(this.categories.indexOf(category) === -1){
    //       this.categories.push(category);
    //     }
    //   })
    // })

    // for now tags are fixed to these disciplines
    this.disciplines = [
      "brand & identity",
      "film & photography",
      "spatial",
      "digital"
    ]

    this.setState({ "hasFilters" : true });
  }

  filterArticles(){
    const allEntries = this.allEntries,
          discipline = this.props.pageContext.discipline;

    let entries;

    // not done in GraphQL due to Craft Tags plugin not working with querying
    if(discipline === undefined){
      entries = allEntries;
    } else {
      entries = allEntries.filter(entry => {
        return entry.disciplines.some(({title}) => title === discipline);
      })
    }

    const jumbledArticles = this.shuffleArray(entries);
    this.filteredArticles = jumbledArticles;

    if(this.filteredArticles.length > 0){
      this.setState({ "hasData" : true });
    }
  }

  shuffleArray(array){
    let checkCookie = this.cookies.get("dwTiles");

    if(checkCookie === undefined){
      let currentIndex = array.length, 
          temporaryValue, 
          randomIndex, 
          randomIndices = new Array(array.length);

      array.forEach((item, i) => {
        item.index = i;
      })

      // While there remain elements to shuffle...
      while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }

      array.forEach((item, i) => {
        randomIndices[i] = item.index;
      })

      // set cookie so tiles only randomised once a day
      let tomorrow = new Date();
      tomorrow.setDate(tomorrow.getDate() + 1);

      this.cookies.set("dwTiles", JSON.stringify(randomIndices),{
        expires: tomorrow
      });
    } else {
      let tempArray = new Array(checkCookie.length);

      checkCookie.forEach((randIndex, i) => {
        tempArray[i] = array[randIndex];
      })
      
      array = tempArray;
    }

    return array;
  }

  render() {
    const seomatic = this.props.data.craft.seomatic,
          hasData = this.state.hasData,
          hasFilters = this.state.hasFilters;

    return (
      <Layout className="work" seo={ seomatic }>
        {hasFilters && (
          <WorkNav render={{ disciplines: this.disciplines, categories: this.categories }}/>
        )}

        {hasData && (            
          <CreateTiles render={{ data: this.filteredArticles }}/>
        )}
      </Layout>
    );
  }
}

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/work") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }
      entries(section: [projects], hidden: false) {
        ... on Craft_Projects {
          hidden
          slug
          title
          previewTile {
            url
            width
            height
            ... on Craft_UploadsVolume {
              url

              imageFile {
                childImageSharp {
                  fluid(maxWidth: 650) {
                    ...GatsbyImageSharpFluid_withWebp_noBase64
                  }
                }
              }

              optimizedimages {
                colorPalette
              }
            }
          }
          description {
            content
          }
          categories {
            title
          }
          disciplines {
            title
          }
        }
      }
    }
  }    
`;