import React, { Component } from "react";
import TransitionLink from "gatsby-plugin-transition-link";
import Img from "gatsby-image";

export default class CreateTiles extends Component {
  data;
  tileImgs = [];
  loadTiles = [];
  fadeTiming = 0.7;
  state = {
    limit : 32
  }

  constructor(props){
    super();

    this.data = props.render.data;
    this.data = this.data.filter(el => {
      return el !== undefined;
    });

    this.data.forEach(({ previewTile }) => {
      const sharpImg = previewTile[0].imageFile.childImageSharp;

      let fluidGatsbyImage = {};

      if(sharpImg !== null){
        fluidGatsbyImage.fluid = { ...sharpImg.fluid };
      } else {
        console.log(previewTile[0].url); // see which ones can't be converted to webp for testing

        fluidGatsbyImage.fluid = { src: previewTile[0].url };
      }

      const backgroundColor = previewTile[0].optimizedimages.colorPalette[0];

      fluidGatsbyImage.backgroundColor = backgroundColor ? backgroundColor : "#efefef"; // so it doesn't break while an image is being processed

      this.tileImgs.push(fluidGatsbyImage);
    })
  }

  componentDidMount(){
    this.mainEl = document.querySelector("main");
    this.loadMore();
  }

  loadMore(e){
    if(e){
      e.preventDefault();
    }

    this.data.forEach((tileData, i) => {
      if(i < this.state.limit){
        this.loadTiles[i] = tileData;
      }
    })

    this.setState({
      limit: (this.state.limit + 8)
    });
  }

  tilesFadeOut(href){
    const clicked = document.querySelector(`.work-tile[href*="${href}"]`);
    clicked.style.transition = `opacity ${this.fadeTiming / 2}s`;
    clicked.style.transitionDelay = `${this.fadeTiming / 2}s`;
    
    this.mainEl.classList.add("-pageFade");
  }

  render() {
    return (
      <React.Fragment>
        <div className="work-tile__container">
          {this.loadTiles.slice( 0, this.state.limit ).map((data, i) => {
            const href = `/work/articles/${data.slug}`;
          
            return(
              <TransitionLink 
                exit={{
                  length: this.fadeTiming,
                  trigger: () => this.tilesFadeOut( href ),
                }}
                entry={{
                  delay: this.fadeTiming * 1.5,
                  length: this.fadeTiming,
                }}
                className="work-tile" 
                key={`home tile ${i}`} 
                to={ href }
              >
                <Img className={"work-tile__image aspect__2-by-1 hover-cursor"} { ...this.tileImgs[i] }/>
                <p className="work-tile__client fontSize-p3 fontSize-uppercase"> { data.title } </p>
                <p className="work-tile__title fontSize-p2" dangerouslySetInnerHTML={{ __html: data.description !== null ? data.description.content : null }}/>
              </TransitionLink>
            )
          })} 
        </div>

        {this.data.length > this.loadTiles.length ? (
          <div className="work-tile__load-more">
            <a href="" onClick={ e => { this.loadMore(e) }} className="work-tile__load-more-button fontSize-p2">Load more work</a>
          </div>
        ) : (
          <div className="work-tile__load-more"/>
        )}
      </React.Fragment>
    );
  }
}